@import url(https://fonts.googleapis.com/css2?family=Muli:wght@300;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Caption:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700;800&display=swap);
@import url(//maxcdn.bootstrapcdn.com/font-awesome/4.3.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #1e1e1e !important;
  overflow: hidden;
}

code {
  font-family: Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  font-size: 16px;
  --text-primary: #ffffff;
  --text-secondary: #E35757;
  --text-title: #08FDC2;
  --bg-primary: #1a1f27;
  --bg-secondary: #0f1014;
  --transition-speed: 300ms;
}

.mainContainer {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.contentBox {
  width: 100%;
  height: 100vh;
  overflow-y: scroll;
}

.contentBox::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px #0f1014;
  -webkit-box-shadow: inset 0 0 6px var(--bg-secondary);
  background-color: transparent;
}

.contentBox::-webkit-scrollbar {
  position: relative;
  width: 10px;
  background-color: #1a1f27;
  background-color: var(--bg-primary);
}

.contentBox::-webkit-scrollbar-thumb {
  background-color: #0f1014;
  background-color: var(--bg-secondary);
}


.tsparticles-canvas-el {
  height: 100vh !important;
}

a, a:hover {
	text-decoration: none;
}

.socialbtns, .socialbtns ul, .socialbtns li {
  margin: 0;
  padding: 5px;
}

.socialbtns li {
    margin: 10px 20px;
    font-size: 1.2rem;
    list-style: none outside none;
    display: inline-block;
}

.socialbtns .fa {
	color: white;
	-moz-border-radius: 22px;
	-webkit-border-radius: 22px;
  -o-border-radius: 22px;
  transition: 100ms;
}

.socialbtns .fa:hover {
	color: #E35757;
	color: var(--text-secondary);
}

@media only screen and (max-width: 600px) {
  .mainContainer {
    flex-direction: column;
    height: 100vh;
    overflow-y: scroll;
  }

}
.navbar {
    background-color: var(--bg-primary);
}

.navbar-nav {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
}

.nav-item {
    width: 100%;
}

.nav-item:last-child {
    margin-top: auto;
}

.nav-link {
    display: flex;
    align-items: center;
    height: 5rem;
    color: var(--text-primary);
    text-decoration: none;
    -webkit-filter: grayscale(100%) opacity(0.7);
            filter: grayscale(100%) opacity(0.7);
    transition: var(--transition-speed);
}

.nav-link:hover {
    -webkit-filter: grayscale(0%) opacity(1);
            filter: grayscale(0%) opacity(1);
    background: var(--bg-secondary);
    color: var(--text-secondary);
}

.link-text {
    display: none;
    margin-left: 1rem;
}

.nav-link svg {
    width: 2rem;
    min-width: 2rem;
    margin: 0 1rem;
    stroke-width: 1.5;
}

.logo {
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 1rem;
    text-align: center;
    color: var(--text-secondary);
    background: var(--bg-secondary);
    font-size: 1.5rem;
    letter-spacing: 0.3ch;
    width: 100%;
}

.logo svg {
    transform: rotate(0deg);
    transition: var(--transition-speed);
}

.logo-text
{
    display: inline;
    position: absolute;
    left: -999px;
    transition: var(--transition-speed);
}

.nav-item:hover svg {
    stroke: var(--text-secondary);
}

/* Small screens */
@media only screen and (max-width: 600px) {
.navbar {
    bottom: 0;
    width: 100vw;
    height: 5rem;
}

.logo {
    display: none;
}

.navbar-nav {
    flex-direction: row;
}

.nav-link {
    justify-content: center;
}

main {
    margin: 0;
}
}

/* Large screens */
@media only screen and (min-width: 600px) {
.navbar {
    top: 0;
    width: 4rem;
    height: 100vh;
}

}

.homeContainer {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100vh;
    align-items: center;
}

.primary {
    width: 50%;
}

.titleName, .titleJob {
    color: white;
    font-family: 'Roboto';
    margin: 0 6rem;
    font-size: 4rem;
    font-weight: bold;
    letter-spacing: 0.05em;
}

.titleJob {
    font-size: 3rem;
    font-weight: 700;
    margin: 1rem 6rem;
}
.animate__bounce {
    -webkit-animation-name: bounce;
    animation-name: bounce;
    transform-origin: center bottom;
}

.infoText {
    font-family: 'Roboto', sans-serif;
    font-size: 2.0rem;
    font-weight: 300;
    color: #c7c7c7;
    margin: 10rem 10rem;
    letter-spacing: 0.15em;
}

.infoText2 {
    font-family: 'Roboto', sans-serif;
    font-size: 1.5rem;
    font-weight: 600;
  
    color: rgb(47, 169, 190);
    margin: 1rem 6rem;
    letter-spacing: 0.2em;
}

.coloured {
    color: rgb(47, 169, 190);
    font-weight: 300;
}

.secondary {
    width: 50%;
}

.avatarImg {
    width: 200%;
    margin: auto;
}

.avatarImg img {
    border: 1.5rem dashed var(--text-secondary);
    border-radius: 50%;
}

.primaryBtn {
    color: rgb(47, 169, 190);
    background-color: transparent;
    border: 2px solid var(--text-title);
    font-family: 'Aclonica', sans-serif;
    font-size: 0.9rem;
    font-weight: 700;
    text-transform: lowercase;
    letter-spacing: 0.10em;
    padding: 5px 20px;
    margin: 5rem 10rem;
    width: 35%;
    transition: 300ms;
}

.primaryBtn:hover {
    background-color: var(--text-secondary);
    color: rgb(236, 212, 212);
}

@media only screen and (max-width: 600px) {
    .homeContainer {
        display: block;
        overflow-y: scroll;
    }
    
    .primary {
        width: 100%;
        padding: 20px 15px;
    }

    .secondary {
        width: 100%;
    }

    .titleName, .titleJob {
        margin: 1rem;
        font-size: 2rem;
        letter-spacing: 0.05em;
    }
    
    .titleJob {
        margin: 1rem;
        font-size: 1.5rem;
        font-weight: 700;
    }
    
    .infoText {
        font-size: 0.9rem;
        margin: 4rem;
    }
    .infoText2 {
        font-size: 1.0rem;
        margin: 1rem;
    }

    .primaryBtn {
        color: rgb(47, 169, 190);
        display: none;
    }
    
  }
.aboutContainer {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100vh;
    align-items: center;
}

.primaryAbt {
    width: 60%;
}

.secondaryAbt {
    width: 40%;
    height: 100vh;
    background-color: #131b25;
}

.titleText {
    color: var(--text-title);
    font-family: 'PT Sans Caption', sans-serif;
    margin: 0 6rem;
    font-size: 4rem;
    font-weight: bolder;
    letter-spacing: 0.02em;
}

.paraText {
    font-family: 'Roboto', sans-serif;
    font-size: 1.1rem;
    font-weight: 300;
    color: white;
    margin: 2rem 6rem;
    letter-spacing: 0.15em;
    line-height: 2rem;
}

/* Skills */
*,
*::after,
*::before {
  box-sizing: border-box;
}

.u-center {
  align-content: center;
  display: flex;
  justify-content: center;
  height: 100%;
}

.skills {
  align-self: center;
  display: flex;
  flex-direction: column;
}

.skill {
  margin: 0.5em 0;
  position: relative;
  width: 22.5em;
}

.skill__type {
  color: #fff;
  font-weight: 300;
  font-size: 0.85rem;
  letter-spacing: 0.145em;
  text-transform: uppercase;
  width: 7em;
}

.skill__outer-bar, .skill__inner-bar {
  height: 0.75em;
}

.skill__outer-bar {
  background: #333;
}

.skill__inner-bar {
  -webkit-animation: fillBar 1s forwards;
          animation: fillBar 1s forwards;
  background: #fff;
  left: 5em;
  transform: scaleX(0);
  transform-origin: 0 0;
}

@-webkit-keyframes fillBar {
  to {
    transform: scaleX(1);
  }
}

@keyframes fillBar {
  to {
    transform: scaleX(1);
  }
}

@media only screen and (max-width: 600px) {
  .aboutContainer {
    display: block;
    overflow-y: scroll;
  }
  .primaryAbt {
    width: 100%;
    padding: 10px 20px;
  }

  .secondaryAbt {
      width: 100%;
      height: 100%;
      display: block;
  }

  .titleText {
    margin: auto;
    font-size: 3rem;
  }

  .paraText {
      font-size: 1rem;
      margin: 1.5rem auto;
      line-height: 1.3rem;
      text-align: justify;
  }

  .u-center {
    display: block;
    margin: auto;
  }

  .skill {
    margin: 1rem auto;
  }

  .skills {
    margin: auto;
  }
}
.pubContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    padding: 0 30px;
    height: 100vh;
}

.cardContainer {
    background-color: white;
    width: 300px;
    margin: 2rem 1rem;
    height: 55vh;
    border-bottom: 5px solid;
}

.cardImg {
    height: 20vh;
    background-color: #fff493;
    background-size: cover;
    background-position: center;
}

.cardBody {
    height: 25vh;
    padding: 10px 15px;
}

.cardTitle {
    font-family: 'PT Sans', sans-serif;
    text-transform: uppercase;
    font-size: 1.1rem;
    font-weight: 700;
    margin: 10px 0;
}

.badge {
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
    font-size: 0.7rem;
    text-transform: uppercase;
    margin-right: 10px;
    padding: 2px 15px;
    border-radius: 5px;
}

.cardDesc {
    font-family: 'Roboto', sans-serif;
    font-size: 0.9rem;
    font-weight: 400;
}

.cardFooter {
    height: 10vh;
    padding: 10px 15px;
}

.cardBtn {
    color: var(--bg-primary);
    background-color: transparent;
    border: 2px solid #0184ff;
    font-size: 0.8rem;
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    padding: 6px 20px;
    transition: 300ms;
    text-decoration: none;
}

.cardBtn:hover {
    background-color: transparent;
    color: white;
    background-color: #0184ff;
    border: 2px solid #0184ff;
}

@media only screen and (max-width: 600px) {
    .pubContainer {
        display: block;
        width: 100%;
    }
    
    .cardContainer {
        width: 100%;
        height: 60vh;
        margin: 20px auto;
    }

    .cardImg {
        height: 20vh;
    }

    .cardBody {
        height: 30vh;
    }

    .cardFooter {
        height: 10vh;
    }
}
.contactCont {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100vh;
    align-items: center;
}

.primaryContact {
    width: 50%;
}

.secondaryContact {
    width: 50%;
    display: flex;
    align-items: center;
    height: 100%;
    background-color: #131b25;
}

.contactContents {
    margin: auto;
    width: 80%;
}

hr {
    margin: 3rem 0 2rem;
}

input {
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    font-weight: 400;
    letter-spacing: 0.1rem;
    width: 100%;
    height: 7vh;
    margin: 5px auto;
    padding: 10px;
    color: aliceblue;
    background-color: transparent;
    border-radius: 2px;
    border: 2px solid rgb(192, 192, 192);
}

textarea {
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    font-weight: 400;
    letter-spacing: 0.1rem;
    width: 100%;
    height: 15vh;
    margin: 5px 0;
    padding: 10px;
    color: aliceblue;
    background-color: transparent;
    border-radius: 2px;
    border: 2px solid rgb(192, 192, 192);
}

.emailBtn {
    color: var(--text-primary);
    background-color: transparent;
    border: 2px solid var(--text-title);
    font-family: 'Open Sans', sans-serif;
    font-size: 0.9rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.15em;
    height: 7vh !important;
    transition: 300ms;
}

.emailBtn:hover {
    background-color: var(--text-title);
    border: 2px solid var(--text-title);
    color: black;
}

@media only screen and (max-width: 600px) {
    .contactCont {
        display: block;
        overflow-y: scroll;
    }

    .primaryContact {
        width: 100%;
        padding: 20px 15px;
    }

    .primaryContact p {
        text-align: left;
    }
    
    .secondaryContact {
        display: block;
        width: 100%;
        padding: 2rem 0;
    }

    .contactContents {
        margin: auto;

    }
}
